html {
  height: 100%;
}
body {
  margin: 0;
  min-height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  overflow: hidden;
}
aside {
  width: 20%;
  height: 100vh;
  display: inline-block;
  vertical-align: top;
  ul {
    width: inherit;
    height: 100vh;
    overflow-y: auto;
    position: fixed;
    list-style: none;
    margin: 0;
    padding: .5rem;
    box-sizing: border-box;
    li {
      padding: .5rem 1rem;
      margin: .3rem 0;
      font-size: 16px;
      font-weight: bold;
      background-color: #c00;
      color: #fff;
      cursor: pointer;
    }
  }
}
main {
  width: 80%;
  height: 100vh;
  overflow-y: auto;
  position: fixed;
  left: 20%;
  display: inline-block;
  vertical-align: top;
  padding: 0 1rem;
  box-sizing: border-box;
}
.articles-number-block {
  padding: 0 1rem 1rem;
}
h1 {
  font-size: 3em;
  padding: 1rem;
  margin: 0;
}
.article {
  padding: 1rem;
  background-color: #f0f0f0;
  margin-bottom: 1rem;
  h2 {
    font-size: 2em;
    padding: 0 0 1rem;
    margin: 0;
  }
  img {
    max-width: 40%;
  }
  .date {
    padding-bottom: .5rem;
  }
}
a {
  color: #c00;
}
.created {
  padding-bottom: 1rem;
}

@media screen and (max-width: 640px) {
  aside {
    width: 100%;
    position: relative;
    z-index: 2;
    background-color: #fff;
	  .expander {
		  position: absolute;
		  display: block;
		  top: 50vh;
		  right: 0;
		  transform: translate(0, -50%);
		  z-index: 2;
		  &:before {
			  display: block;
			  content: '<';
			  font-size: 1.5em;
			  font-weight: bold;
			  color: #c00;
			  background-color: #fff;
			  border: 1px solid #c00;
			  border-radius: 0.5em 0 0 0.5em;
			  padding: 3em 0.2em;
		  }
	  }
	  &.collapsed {
		  width: 0;
		  overflow: visible;
		  ul {
			  display: none;
		  }
		  .expander {
			  right: auto;
			  left: 0;
			  transform: translate(0, -50%);
			  &:before {
				  content: '>';
				  border-radius: 0 0.5em 0.5em 0;
			  }
		  }
	  }
  }
  main {
    width: 100%;
    left: 0;
  }
  .article {
	  img {
		  max-width: 100%;
		  width: 100%;
	  }
  }
}
